import { motion } from "framer-motion"
import { projectViewStyle, projectGalleryScrollViewStyle, items, viewButtonStyle, galleryContainerStyle, galleryVariants } from "../styles/ProjectsViewStyle"
import { MenuSideBar } from "./MenuSideBar"
import eventreminder from "../images/eventreminder.png"
import poke from "../images/poke.png"
import nftsq from "../images/nftsq.png"
import compass from "../images/compass.png"
import { variants, projectCardStyle } from "../styles/ProjectsViewStyle"
import { transition } from "../styles/Animation"
import { Project } from "../types/types";
import { ProjectDetailView } from "./ProjectDetailView"
import { useState, useRef, useEffect } from "react"


export function ProjectsView() {


    const projects: Project[] = 
    [{name: "EventReminder", img: eventreminder,width: 200,thumbprintWidth: 150, id: 1, },
    {name: "myPokedex", img: poke, width: 200, thumbprintWidth: 150,id: 2},
    {name: "NFT Query", img: nftsq, width: 200, thumbprintWidth: 150, id: 3},
    {name: "Compass", img: compass, width: 300, thumbprintWidth: 290, id: 4}]
    const[shownProject, setShownProject] = useState(projects[0])

    const [ready, setReady] = useState(document.readyState === 'complete')
    
    useEffect(() => {
      const onPageLoad = () => {
        setReady(true)
      };
  
      // Check if the page has already loaded
      if (document.readyState === 'complete') {
        onPageLoad();
      } else {
        window.addEventListener('load', onPageLoad);
        // Remove the event listener when component unmounts
        return () => window.removeEventListener('load', onPageLoad);
      }
    }, []);
    
    return <motion.div 
    variants={variants} 
    initial="hidden"
    animate="visible"
    style={projectViewStyle as React.CSSProperties}>
    <MenuSideBar></MenuSideBar>

    
    {ready && (
<>
<ProjectGalleryScrollView projects={projects} setShownProject  = {(val: Project) => setShownProject(val)}></ProjectGalleryScrollView>

<ProjectDetailView img = {shownProject.img} width = {shownProject.width}></ProjectDetailView>
</>
    )

    }




    </motion.div>
}

function ProjectGalleryScrollView(props: {setShownProject: (e: Project) => void, projects: Project[]}) {

  const [currentRef, setRef] = useState<number | undefined>(1)



  return <div style={galleryContainerStyle}>
  <motion.div 
  variants={galleryVariants}
  initial="hidden"
  animate="visible"
  id = "projectGallContainer" 
  style={projectGalleryScrollViewStyle as React.CSSProperties}>

    {props.projects.map((item, index) => (
          <ProjectCard  id = {item.id} key={index} width={item.thumbprintWidth} name={item.name} img={item.img} refa={currentRef} onClick={function (e): void {
            console.log(currentRef)
            setRef(item.id)
            console.log(item)
            props.setShownProject(item)
          } }></ProjectCard>
    ))
    }
  </motion.div>
  </div>
  
}

function ProjectCard({name, img, width, refa, onClick, id}: Project & {refa: number | undefined, onClick: (e? : number) => void}) {
  return <motion.div
  style={{position: 'relative'}}
  >
    <motion.div  
    style={projectCardStyle as React.CSSProperties}
    transition={{duration: .8}}
    variants={items}>
    <img width= { width } src={img}></img>
    <motion.button 
    onTap={() => {
      onClick(id)
    }}
    style = {{...viewButtonStyle, background: refa === id ? '#FA4A0C' : 'none', color: refa === id ? 'white' : 'black',fontSize: 14, padding: '12px 20px', fontWeight: 600}}>
    {name}

    </motion.button>
    </motion.div>
  </motion.div>
}