import { motion } from "framer-motion"
import poke from "../images/poke.png"
import { transition } from "../styles/Animation"
import {items, projectViewContainerStyle, projectGridCardStyle, variants, cardItems } from "../styles/ProjectViewDetailStyle"
import { ProjectDetails } from "../types/types"
import { useScroll } from "framer-motion"
import { useEffect, useRef, useState } from "react"

export function ProjectDetailView({img, width} : ProjectDetails) {
    return <motion.div style = {projectViewContainerStyle}>
    <ProjectDetailGrid img = {img} width={width}></ProjectDetailGrid>
    </motion.div>
}
 

function ProjectDetailGrid({img, width} : ProjectDetails) {

    const scrollRef = useRef(null)
   


    return <motion.div 
    ref={scrollRef}
    key={img}
    variants={variants}
    initial="hidden"
    animate="visible"
    id = "project-grid-container"
    >

    <ProjectGridItemContent width = {width} img={img} scrollRef={scrollRef}></ProjectGridItemContent>
    <ProjectGridItemContent width = {width}img={img} scrollRef={scrollRef}></ProjectGridItemContent>
    <ProjectGridItemContent width = {width} img={img} scrollRef={scrollRef}></ProjectGridItemContent>
    
    </motion.div>
}


function ProjectGridItemContent({img, scrollRef, width}: ProjectDetails) {

    const {scrollYProgress, scrollY} = useScroll()
    const gridItemRef = useRef<HTMLDivElement>(null)
    const [opacity, setOpacity] = useState(0)


    var newOpacity = () => {

        var opac = ((scrollY as any).current + window.innerHeight) as number / (gridItemRef.current!.offsetTop + gridItemRef.current!.scrollHeight) as number
        if (opac >= .8) {
            return 1
        } else {
            return opac * 2
        }
    }

    function manageOpacity() {
        setOpacity( gridItemRef.current ?  newOpacity() : 0 )
    }

    useEffect(() => {
        manageOpacity()
        document.addEventListener('scroll', () => {
        manageOpacity()
        }, true)
        return () => window.removeEventListener('scroll',manageOpacity)
    },[])


    return <motion.div 
    
    variants={cardItems(opacity)}
    ref = {gridItemRef}
    initial="hidden"
    whileInView="visible"
    viewport={{ once: true}}
    transition={{duration: .2}}
    className="project-grid-item"
    style={projectGridCardStyle}>

    <motion.img 
    initial={{ opacity: 0, y: 150 }}
    whileInView={{ opacity: 1, y: 0 }}
    viewport={{ once: false }}
    transition={{duration: .5}}

    width={width}     
    src={img}>
    </motion.img>
    </motion.div>
}